export class Holding {
    base: string;
    quantity: number;
	price: number;
    value: number;
    volume: number;
    change: number;

    constructor(base: string = "btc", quantity: number = 0) {
        this.base = base;
        this.quantity = quantity;
        this.value = 0;
		this.price = 0;
        this.volume = 0;
        this.change = 0;
    }
}


